.menu {
  box-sizing: border-box;

  height: 100vh;
  background-color: #e5e6e8;
  font-weight: 700;

  overflow: auto; /* to scroll the menu if needed */

  &__top {
    margin-bottom: 56px;

    position: sticky; /* to keep cross always visible */
    top: 0;
    z-index: 1;
    background-color: #e5e5e5;
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__content {
    @include content-padding-inline;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;

    color: inherit;
    text-decoration: none;
    font-size: 16px;
    line-height: 22.4px;
    letter-spacing: 2px;
  }

  &__call-to-order {
    display: block;
    width: fit-content;
    padding-bottom: 7px;
    border-bottom: 1px solid $main-text-color;

    color: inherit;
    text-decoration: none;
    font-size: 16px;
    line-height: 22.4px;
    letter-spacing: 2px;
    text-transform: uppercase;
    position: relative;
  }
}
